import React, { useState, useEffect } from "react";
import "./MessagesBar.scss";
import { socket } from "../../service/socket";
const messagesTest = [
  { time: "17:05", text: "Hello world!", username: "xyz" },
  { time: "13:45", text: "Równo", username: "Jeż" },
];
export const MessagesBar = (props) => {
  const [messages, setMessages] = useState([]);

  const [sectionRef, setSectionRef] = useState(null);

  useEffect(() => {
    if (sectionRef) {
      console.log("ELOOOOO", sectionRef.scrollHeight);

      sectionRef.scrollTop = sectionRef.scrollHeight;
    }
    // window.scrollTo(sectionRef);
    console.log(">>", sectionRef);
  }, [messages]);

  useEffect(() => {
    setMessages(props.messages);
  }, [props.messages]);

  const renderMessages = () => {
    const listMessages = messages.map((message, index) => (
      <li key={index} className="message">
        <p className="message__username">
          {message.username}
          <span className="message__time"> {message.time}</span>
        </p>
        <p className="message__text"> {message.text}</p>
      </li>
    ));

    return listMessages;
  };

  return (
    <div className="messages-bar">
      <label className="messages-bar__header">Messages</label>
      <div className="messages-bar__list-wrapper">
        <ul
          className="messages-bar__list"
          ref={(el) => {
            // this.messagesEnd = el;
            setSectionRef(el);
          }}
        >
          {messages && renderMessages()}
        </ul>
      </div>
    </div>
  );
};
